// FOOTER :: START

.footer {
	background-color: #373656;

	&__wrapper {
		display: flex;

		&-1 {
			padding-top: 45px;
			padding-bottom: 35px;

			.footer__wrapper {
				&-left {
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					width: 100%;
					max-width: 353px;
				}

				&-right {
					display: flex;
					flex: 1;
					min-width: 1px;
					max-width: 100%;

					> div {
						&:not(:last-of-type) {
							padding-right: 70px;
						}
					}
				}
			}
		}

		&-2 {
			align-items: center;
			justify-content: space-between;
			min-height: 55px;
			border-top: 1px solid #4b4a67;

			.footer__wrapper {
				&-left {
					display: flex;
					align-items: center;
				}
			}
		}
	}

	&__title {
		padding-bottom: 25px;
		color: #ffffff;
		font-size: 20px;
		font-weight: bold;
		text-transform: uppercase;
	}

	&__nav {
		display: flex;
		align-items: flex-start;
		flex-direction: column;

		&-link {
			white-space: nowrap;
			position: relative;
			display: flex;
			color: #ffffff;
			font-size: 16px;

			&:not(:last-of-type) {
				margin-bottom: 10px;
			}

			&:hover {
				color: #ffffff;

				&:before,
				&:after {
					width: 50%;
					@include transition(width);
				}
			}

			&:before,
			&:after {
				content: '';
				position: absolute;
				top: 100%;
				width: 0;
				height: 1px;
				background-color: #ffffff;
				@include transition(width);
			}
			&:before {
				left: 50%;
			}
			&:after {
				right: 50%;
			}
		}
	}
}

// FOOTER :: END
